import React from 'react';
import {NavLink} from 'react-router-dom';
const Home = () =>{
  
    return (
        <>
         <h1 className="home-title text-center  my-5 text-white">Subjects</h1>
          <div className="container">
         <div className="row row-cols-1 row-cols-sm-3 g-4 align-item-center justify-content-center">
                    <div className="card mx-2" style={{maxWidth:"18rem"}}> 
                      <div className="card-body">
                        <h3 className="card-title">IT-Tools and Web designing</h3>
                        <p className="card-text">Learn about IT-Tools and Web designing from my curriculum. I hope this Documentation will help you.</p>
                        <NavLink to="/B.Sc.IT/it-tools" className="btn btn-outline-dark">Learn More</NavLink>
                      </div>
                    </div>
                    <div className="card mx-2 bg-success" style={{maxWidth:"18rem"}}> 
                      <div className="card-body">
                        <h3 className="card-title text-white">Android Development</h3>
                        <p className="card-text text-white">Learn about Android application development from my curriculum. I hope this Documentation will help you.</p>
                        <NavLink to="/B.Sc.IT/android" className="btn btn-outline-light">Learn More</NavLink>
                      </div>
                    </div>
                    <div className="card mx-2 bg-dark" style={{maxWidth:"18rem"}}> 
                      <div className="card-body">
                        <h3 className="card-title text-white">C++ Programming</h3>
                        <p className="card-text text-white">Learn about C++ programming language from my curriculum. I hope this Documentation will help you.</p>
                        <NavLink to="/B.Sc.IT/cpp" className="btn btn-outline-light">Learn More</NavLink>
                      </div>
                    </div>
                    
                </div>
                <br/><br/>
         </div>
        </>
    );
};

export default Home;