import React, { useState } from 'react';
import { db } from './Fire';
const Contact = () => {

    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [message, setMessage] = useState("");

    const handleSubmit = (e)=>{
            e.preventDefault();
            db.collection("contact").add({
                name:name,
                email:email,
                message:message
            })
            .then(()=>{
                alert("Message has been Submitted");
            })
            .catch(error =>{
                alert(error);
            })
            setName("");
            setEmail("");
            setMessage("");
    }

    return (
        <>
            <div className="container contact-box justify-content-center" style={{ marginTop: "5em" }} >
                <h1 className="contact-us text-center mb-3">Contact Us </h1>
                <div className="container col-md-5">
                <form className="row g-3" onSubmit={handleSubmit}>
                      <div className="mb-1">
                                <label for="exampleFormControlInput1" className="form-label text-white">Your name</label>
                                <input type="text" className="form-control" id="exampleFormControlInput1" value={name} onChange={(e)=> setName(e.target.value)} required/>
                                </div>
                 <div className="mb-1">
                                <label for="exampleFormControlInput2" className="form-label text-white">Email address</label>
                                <input type="email" className="form-control" id="exampleFormControlInput2" value={email} onChange={(e)=> setEmail(e.target.value)} required/>
                                </div>
                 <div className="mb-1">
                                <label for="exampleFormControlTextarea1" className="form-label text-white">Message</label>
                                <textarea value={message} onChange={(e)=> setMessage(e.target.value)} className="form-control" id="exampleFormControlTextarea1" rows="3" required></textarea>
                                </div>
                            <div className="align-items-center mt-4">
                                <button type="submit" className="btn btn-primary w-50">Send</button>
                                </div>
                        </form>
                        </div><br />
            </div><br />
        </>
    );
}

export default Contact;